.projects-wrapper {
  height: fit-content;
  background-color: #131313;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  position: relative;
  padding: 5px;
  padding-bottom: 1rem;
}
.projects-wrapper > h1 {
  color: #00a2ff;
  font-size: 2.5rem;
  width: 100%;
  text-align: center;
  padding: 5px 0;
  margin-bottom: 1rem;
}

.projects {
  height: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  background-color: #202020;
  width: 100%;
  border-radius: 10px;
  background-image: radial-gradient(circle at left top, rgba(0, 0, 0, 0.2), #202020);
}

.project-card {
  border-radius: 6px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: fit-content;
  place-self: center;
  cursor: pointer;
  width: 90%;
  max-width: 325px;
  height: 100px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background-color: #202020;
  position: relative;
  margin-top: 1rem;
}
.project-card > .project-card-header {
  display: grid;
  grid-template-columns: 3fr 1fr;
  position: relative;
  width: 100%;
}
.project-card > .project-card-header > div:first-child {
  background-color: #242424;
}
.project-card > .project-card-header > h2 {
  background-color: #131313;
  padding: 0.5rem 0;
  font-size: min(120%, 50px);
  width: 100%;
  text-align: center;
}
.project-card:last-child {
  margin-bottom: 1rem;
}
.project-card > p {
  color: rgba(255, 255, 255, 0.59);
  padding: 0.5rem 0.5rem;
  font-size: 0.9rem;
  font-style: italic;
  position: absolute;
  background-color: #202020;
  top: 37px;
  height: 50px;
  width: 95%;
  max-width: 500px;
  z-index: 100;
}
.project-card > p > b {
  color: white;
  font-size: 0.8rem;
}
.project-card > p > a {
  text-decoration: none;
  color: rgb(97, 97, 244);
}
.project-card > p > a:hover {
  text-decoration: underline;
}
.project-card > img {
  padding: 1rem;
  background-color: white;
  object-fit: cover;
  width: 100%;
}
.project-card.gitto > .project-card-header img {
  width: 80px;
}
.project-card.gitto > .project-card-header > h2 {
  color: #ff6b00;
}
.project-card.aad > .project-card-header img {
  margin: 0 0.25rem;
}
.project-card.aad > .project-card-header > h2 {
  color: #004cbe;
}
.project-card.url-shorten > .project-card-header > div {
  display: flex;
  align-items: center;
  overflow-x: hidden;
}
.project-card.url-shorten > .project-card-header .url-animation {
  width: 10px;
  height: 20px;
  background-color: red;
  border-radius: 4px;
  animation: url-anim 3.5s infinite linear;
}
.project-card.url-shorten > .project-card-header > h2 {
  color: green;
}

.open-project-card {
  position: absolute;
  background-color: white;
  top: 40%;
  backdrop-filter: blur(10px);
  max-width: 80%;
}

.blur {
  width: 100vw;
  height: 100vh;
  background-color: black;
  opacity: 0.1;
  position: absolute;
}

@keyframes url-anim {
  0% {
    width: 150px;
    transform: translateX(-200px);
  }
  40% {
    width: 150px;
    transform: translateX(50px);
  }
  60% {
    background-color: green;
    width: 50px;
    transform: translateX(50px);
  }
  100% {
    transform: translateX(250px);
    background-color: green;
    width: 50px;
  }
}
@media (min-width: 800px) {
  .projects-wrapper {
    padding: 15px;
  }

  .projects {
    display: grid;
    place-items: center;
    width: 100%;
    max-width: 800px;
    grid-template-columns: 1fr 1fr;
  }
}

